/* function */
@keyframes _caDYj {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.02);
  }
}

/* component */
._1ydHA {
  display: none;
}

._1ydHA + label {
  --size: 90%;
  --radius: 50%;
  border: 3px solid white;
  border-radius: var(--radius);
  cursor: pointer;
  display: inline-block;
  height: 50px;
  width: 50px;
  position: relative;
  margin-bottom: 0px;
}

._1ydHA + label:before {
  background-color: red;
  border-radius: var(--radius);
  bottom: 0;
  content: '';
  height: var(--size);
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
  transition: all .25s cubic-bezier(1, 0, 0, 0.2);
  width: var(--size);
}

._1ydHA:checked + label {
  animation: _caDYj .25s;
  animation-duration: .15s;
}

._1ydHA:checked + label:before {
  --size: 50%;
  --radius: 10%;
  transform: rotateZ(180deg);
}

